import React from "react";
import Card from "./card";

const CardList = ({robots}) =>{
  const CardComponent = robots.map((user,i)=>{
    return <Card key={robots[i].name} name={robots[i].name} email={robots[i].email} />
  });
  return(
    <div>
      {CardComponent}
      </div>

  );
}
export default CardList;



